import { observer, Observer } from "mobx-react";
import React from "react";
import { Popup, Menu, MenuItemProps } from "semantic-ui-react";
import { ActionableMetaData, ActionID, ISelector } from "../../interfaces/SelectableInterfaces";

interface ContextMenuProps {
  selectorViewModel: ISelector;
  isContextOpen: boolean;
  setIsContextOpen: React.Dispatch<React.SetStateAction<boolean>>
}

const ContextMenu = observer((props: ContextMenuProps) => {
  React.useEffect(() => {
    window.addEventListener("contextmenu", onWindowContextMenu);

    return () => {
      window.removeEventListener("contextmenu", onWindowContextMenu);
    }
  })

  const contextRef = React.useRef<any>();

  const [menuItems, setMenuItems] = React.useState<MenuItemProps[]>([]);

  const getContextMenuItems = (): MenuItemProps[] => {
    const useLanguagePlural = props.selectorViewModel.getSelected().length > 1;
    const actionItems = props.selectorViewModel.getSelected().map(selectable => selectable.getActions());

    const menuItems = [
      ...actionItems.reduce(
        (prev: Map<ActionID, VoidFunction[]>, curr) => {
          curr.forEach((value, key) => {
            if (!prev.get(key)) prev.set(key, []);
            if (!value) return;

            prev.get(key)?.push(value);
          })
          return prev;
        }, new Map<ActionID, VoidFunction[]>()
      ).entries()
    ]
    .filter(([_, fnArray]) => fnArray.length > 0)
    .map(([key, fnArray]) => {
      const action = ActionableMetaData.get(key);
      if (!action) return {};
      return {
          key: action.key,
          content: useLanguagePlural ? action.pluralName : action.name,
          icon: action.icon,
          onClick: () => {
            fnArray.forEach((fn) => fn.call(null));
          }
        } as MenuItemProps
    });

    return menuItems;
  }

  const openContext = (event: MouseEvent) => {
    contextRef.current = {
      getBoundingClientRect: () => ({
        left: event.clientX,
        top: event.clientY,
        right: event.clientX + 1,
        bottom: event.clientY + 1,
        height: 0,
        width: 0,
      }),
    }
    props.setIsContextOpen(true);
  }

  const onWindowContextMenu = (event: MouseEvent) => {
    if (event.defaultPrevented) return;

    const selected = props.selectorViewModel.getSelected();
    if (selected.length === 0) {
      // Don't use context when no items selected. Possibly change this.
      return;
    } else {
      // Show context based on selected items
      setMenuItems(getContextMenuItems());
    }

    event.preventDefault();

    openContext(event);
  }

  return <Observer>
    {() => {
      return (
        <Popup
          basic
          context={contextRef}
          onClose={() => props.setIsContextOpen(false)}
          open={props.isContextOpen}
        >
          <Menu
            items={menuItems}
            onItemClick={(e) => {
              e.preventDefault();
              props.setIsContextOpen(false);
            }}
            secondary
            vertical
          />
        </Popup>
      )
    }}
  </Observer>
})

export default ContextMenu;