import React from "react";
import { Box, boxesIntersect, useSelectionContainer } from "react-drag-to-select";
import { ISelectableEntity, ISelector } from "../../interfaces/SelectableInterfaces";
import { wasToggleInSelectionGroupKeyUsed } from "../../helpers/EventUtils";

interface RegionSelectProps {
  selectorViewModel: ISelector,
  setIsRegionSelecting: (b: boolean) => void;
  draggableItemsMap: Map<ISelectableEntity, HTMLDivElement>,
}

const RegionSelect = (props: RegionSelectProps) => {
  React.useEffect(() => {
    window.addEventListener("mousedown", onMouseDown);
    window.addEventListener("mouseup", onMouseUp);

    return () => {
      window.removeEventListener("mousedown", onMouseDown)
      window.removeEventListener("mouseup", onMouseUp);
    }
  })

  const onMouseDown = (event: MouseEvent) => {
    if (isToggleSelectionKeyUsed) return;
    if (wasToggleInSelectionGroupKeyUsed(event)) setIsToggleSelectionKeyUsed(true)
  }
  const onMouseUp = (event: MouseEvent) => {
    setIsToggleSelectionKeyUsed(false)
  }

  const draggableBoxesRef = React.useRef<[ISelectableEntity, Box][]>([]);

  const [isToggleSelectionKeyUsed, setIsToggleSelectionKeyUsed] = React.useState(false);
  const [wasInRegion, setWasInRegion] = React.useState<ISelectableEntity[]>([]);

  const onSelectionStart = () => {
    props.setIsRegionSelecting(true);
    draggableBoxesRef.current = [...props.draggableItemsMap.entries()].reduce((previous: [ISelectableEntity, Box][], [selectable, div]): [ISelectableEntity, Box][] => {
      const rect = div.getBoundingClientRect();
      return [
        ...previous,
        [selectable, rect]
      ];
    }, []);
  }

  const onSelectionChangeWithToggleKey = (box: Box) => {
    draggableBoxesRef.current
      .filter(([selectable, draggableBox]) => {
        const alreadyInRegion = wasInRegion.includes(selectable);
        const nowInRegion = boxesIntersect(box, draggableBox);

        // Item not in region and we don't need to worry about it, or item already processed
        if (alreadyInRegion === nowInRegion) return false;

        if (alreadyInRegion && !nowInRegion) {
          // Draggable escapes a region, toggle it and remove from items in region.
          setWasInRegion(prevSelected => prevSelected.filter((prevSelectable) => prevSelectable !== selectable));
        }

        if (!alreadyInRegion && nowInRegion) {
          // Draggable enters a region, toggle it and add to items in region.
          setWasInRegion(prevSelected => [...prevSelected, selectable]);
        }
        return true;
      }, [])
      .forEach(([courseModel, _]) => props.selectorViewModel.onToggle(courseModel));
  }

  const onSelectionChangeWithoutToggleKey = (box: Box) => {
    draggableBoxesRef.current
      .filter(([selectable, draggableBox]) => {
        const isSelected: boolean = props.selectorViewModel.getSelected().includes(selectable);
        const isIntersecting: boolean = boxesIntersect(box, draggableBox);

        return (isSelected !== isIntersecting);
      }, [])
      .forEach(([selectable, _]) => props.selectorViewModel.onToggle(selectable));
  }

  const onSelectionEnd = () => {
    draggableBoxesRef.current = [];
    setWasInRegion([]);
  }

  const { DragSelection } = useSelectionContainer({
    onSelectionStart,
    onSelectionEnd,

    onSelectionChange: React.useCallback(
      isToggleSelectionKeyUsed
        ? onSelectionChangeWithToggleKey
        : onSelectionChangeWithoutToggleKey
      , [isToggleSelectionKeyUsed, wasInRegion]
    ),
  });

  return (<DragSelection />)
}

export default RegionSelect;