import { SemanticICONS } from "semantic-ui-react/dist/commonjs/generic";

export interface IActionMetaData {
  key: string,
  icon: SemanticICONS,
  name: string,
  pluralName?: string,
}

export enum ActionID {
  REMOVE_COURSE,
  PLAN_COURSE,
  COMPLETE_COURSE,
}

export type Actions = Map<ActionID, undefined | VoidFunction>;

export const ActionableMetaData = new Map<ActionID, IActionMetaData>([
  [ActionID.REMOVE_COURSE, {
    key: "remove",
    icon: "delete calendar",
    name: "Remove course from planner",
    pluralName: "Remove all courses from planner",
  }],
  [ActionID.PLAN_COURSE, {
    key: "plan",
    icon: "bookmark",
    name: "Move course to planned",
    pluralName: "Move all courses to planned",
  }],
  [ActionID.COMPLETE_COURSE, {
    key: "complete",
    icon: "checked calendar",
    name: "Move course to completed",
    pluralName: "Move all courses to completed",
  }],
])

export interface ISelectableEntity {
  getActions: () => Actions
}

export interface ISelector {
  getSelected: () => ISelectableEntity[];
  unselectAll: () => void;
  onToggle: (selectable: ISelectableEntity) => void;
}
